.section--project {
    margin-top: 50px;
    padding: 110px 20px 0;
}
.section--project .inner {
    max-width: 1600px;
    margin: 0 auto;
}
.section--project .project-title {
    font-size: 60px;
    color: #f1f2f3;
}
.section--project .project-subtitle {
    margin-top: 50px;
    font-size: 25px;
    color: #c1c2c3;
    font-weight: 300;
}
.section--project .project-cards {
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(auto-fit, 32%);
    column-gap: 2%;
}

@media screen and (max-width: 1380px) {
    .section--project {
        padding: 90px 20px 0;
    }
    .section--project .project-title {
        font-size: 45px;
    }
    .section--project .project-subtitle {
        font-size: 20px;
    }
    .section--project .project-cards {
        grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    }
}
@media screen and (max-width: 768px) {
    .section--project .project-title {
        font-size: 32px;
        text-align: center;
        font-weight: 500;
    }
    .section--project .project-subtitle {
        text-align: center;
    }
    .section--project .project-cards {
        grid-template-columns: repeat(auto-fit, 100%);
    }
}

.modal-wrapper {
  position: fixed;
  top:0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10000;
}

.modal-wrapper .modal-background {
  position: fixed;
  top:0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10000;
  background-color: rgba(0,0,0,0.7);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-wrapper .modal-card {
  padding: 3rem;
  width: 50vw;
  min-height: 55vh;
  max-height: 75vh;
  background-color: #212223;
  border-radius: 0.5rem;
  color: #fff;
  overflow-y: auto;
  scrollbar-width: none;
}


.modal-card h2 {
  font-size: 2.1rem;
  padding-bottom: 2.4rem;
}
.modal-card h2 a {
  font-size: 1.4rem;
  /* padding-left: 0.5rem; */
  text-decoration: none;
  color: #888
}

.modal-card ul {
  width: 100%;
  overflow-x: scroll;
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 0 2.4rem;
  scrollbar-width: none;
}
.modal-card ul li img{
  width: 10rem;
  display: block;
}
.modal-card ul li .big-image {
  width: 25rem;
}

.modal-card .tag-wrapper {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  padding-bottom: 2.4rem;
  width: 100%;
  overflow-x: auto;
  scrollbar-width: none;
}
.tag-wrapper .modal-tag {
  padding: 0.2rem 0.4rem;
  border-radius: 0.4rem;
  background-color: #687;
}
.modal-card .modal-description {
  padding-bottom: 1.6rem;
  font-size: 1.1rem;
}